.toggle {
   &__wrapper {
      margin-bottom: 50px;
   }

   &__item {
      width: 100%;
      display: table;
      margin: 0 auto 50px;
      box-shadow: 11px 4px 14px 0px rgba(0, 0, 0, 0.5);
      padding: 15px 15px 0;
      cursor: pointer;

      @media screen and (min-width: $medium) {
         width: 71.63%;
      }
   }

   &__header {
      display: flex;
      align-items: center;
      width: 100%;
      padding-bottom: 15px;

      &.m-active {
         .toggle__icon {
            transform: rotate(-90deg);
         }
      }
   }

   &__footer {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      padding: 15px 0;
   }

   &__icon {
      font: {
         size: 30px;
      }
      color: green;
   }

   &__title {
      font: {
         size: 32px;
         weight: 700;
         family: $primary-font-family;
      }
      padding: 0 0 0 20px;

      &:hover {
         text-decoration: underline;
      }
   }
}
