.nav {
   padding: 0 5px;
   min-height: 20px;
   display: flex;
   align-items: center;

   &__list {
      display: flex;
   }

   &__btn {
      margin-left: 10px;
   }

   &__item {
      margin: 0 10px;
      color: $color-text;
   }

   &__link {
      display: block;
      color: $color-text;
      text-transform: uppercase;
      text-decoration: none;
      position: relative;
      font: {
         size: 10px;
         family: $custom-font-family;
         weight: 700;
      }
   }

   &__icon {
      transition: 0.3s;
   }

   &.m-primary {
      .nav__list {
         flex-direction: column;
      }

      .nav__item {
         margin-bottom: 10px;
      }

      .nav__link {
         color: rgb(119, 119, 119);
         text-transform: none;
         font: {
            family: $custom-font-family;
            size: 13px;
            weight: 400;
         }

         &:hover {
            color: #aaa;
            text-decoration: underline;
         }
      }
   }

   &.m-secondary {
      position: fixed;
      left: 0;
      top: calc(-100% + 40px);
      width: 100%;
      background: $background-dark;
      padding: 50px 0;
      transition: 0.5s;

      .nav__list {
         flex-direction: column;
         align-items: center;
         justify-content: center;
         width: 100%;
      }

      .nav__item {
         margin-bottom: 20px;
      }

      .nav__link:before {
         content: "";
         position: absolute;
         bottom: -5px;
         left: 0;
         width: 100%;
         height: 2px;
         background: $link-hover;
         transform: scale3d(0, 5, 1);
         transform-origin: 0% 50%;
         transform-origin: 0% 50%;
         transition: transform 0.3s cubic-bezier(1, 0.68, 0.16, 0.9);
      }

      .nav__item.current-menu-item .nav__link,
      .nav__item.current_page_item .nav__link,
      .nav__item.m-active .nav__link,
      .nav__link:hover {
         color: $link-hover;
      }

      .nav__item.current-menu-item .nav__link:before,
      .nav__item.current_page_item .nav__link:before,
      .nav__item.m-active .nav__link:before,
      .nav__link:hover:before {
         content: "";
         transform: scale3d(1, 1, 1);
      }

      @media screen and (min-width: $large) {
         position: static;
         display: flex;
         padding: 0 20px;

         .nav__list {
            flex-direction: row;
            align-items: center;
            justify-content: center;
            width: 100%;
         }

         .nav__item {
            margin-bottom: 0;
         }
      }

      &.m-active {
         top: 40px;

         .nav__list {
            flex-direction: column;
            align-items: center;
            justify-content: center;
         }

         .nav__item {
            margin-bottom: 20px;
         }
      }
   }

   &__wrapper {
      display: flex;
      justify-content: flex-end;
      align-items: center;
   }

   &.m-social {
      padding-right: 0;

      .nav__link {
         font: {
            size: 20px;
         }
         line-height: 1;
         text-align: center;

         &:hover {
            .fa-facebook-square {
               color: #4a73c9;
            }

            .fa-instagram {
               color: #3f729b;
            }

            .fa-youtube {
               color: #c4302b;
            }
            .nav__icon {
               transform: scale(0.9);
            }
         }
      }

      @media screen and (min-width: $medium) {
         border-left: 1px solid $border;
      }
   }

   &.m-mobile {
      .nav__link {
         font: {
            size: 18px;
         }
      }

      @media screen and (min-width: $medium) {
         border-left: 1px solid $border;
      }

      @media screen and (min-width: $large) {
         display: none;
      }
   }

   &.m-simple {
      border-left: none;
      padding-left: 0;
   }

   &.m-large {
      .nav__item {
         margin: 0 15px 0 0;

         @media screen and (min-width: $medium) {
            margin: 0 50px 0 0;
         }
      }

      .nav__link {
         font: {
            size: 60px;
         }
      }
   }

   @media screen and (min-width: $medium) {
      padding: 0 20px;
   }
}
