.shape {
    position: absolute;
    
    &__img {
      position: relative;
      width: 100%;
      height: 25px;
      fill: $background-bright;
    }
    
    &__btn {
      width: 300px;
      position: absolute;
      left: 50%;
      height: 35px;
      bottom: 0px;
      z-index: 2;
      transform: translateX(-50%);
    }
    
    &.shape_primary {
      height: 25px;
      bottom: 0;
      left: 0;
      width: 100%;
    }
    
    &.shape_secondary {
      height: 25px;
      top: 0;
      left: 0;
      width: 100%;
    }
  }