.widget {
   &__wrapper {
      margin: 0 -10px 80px;
   }

   &__header,
   &__header p {
      line-height: 1.6;
      margin: 0;
      padding: 0;
      font: {
         size: 20px;
      }
      @media screen and (min-width: $medium) {
         font: {
            size: 28px;
         }
         line-height: 1.1;
      }
   }

   &__header {
      &_mobile {
         display: block;
         @media screen and (min-width: $medium) {
            display: none;
         }
      }
      &_desktop {
         display: none;
         @media screen and (min-width: $medium) {
            display: block;
         }
      }
   }

   &__list {
      display: flex;
      flex-wrap: wrap;

      &.m-secondary {
         .widget__item {
            width: 100%;
            margin: 0 0 15px 0;
            @media screen and (min-width: $medium) {
               width: 50%;
            }
         }
      }
   }

   &__item {
      width: 100%;
      min-height: 330px;
      padding: 45px;
      margin: 10px;
      position: relative;
      cursor: pointer;
      perspective: 700px;
      transform: translateZ(0);

      &:hover {
         .widget__face {
            transform: rotateY(180deg);
         }

         .widget__back {
            transform: rotateY(360deg);
         }
      }

      @media screen and (min-width: $medium) {
         width: calc(50% - 20px);
      }

      @media screen and (min-width: 1023px) {
         width: calc(25% - 20px);
      }
   }

   &__title {
      text-align: center;
      text-transform: uppercase;
      font: {
         family: $primary-font-family;
         weight: 700;
         size: 20px;
      }
   }

   &__back,
   &__face {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 20px;
      border: 1px solid $border-secondary;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      backface-visibility: hidden;
      transition: transform 0.6s cubic-bezier(0.2, 0.85, 0.4, 1.275);
      text-align: center;
   }

   &__face {
      transform: rotateY(0);
      background: $background-bright;
   }

   &__back {
      transform: rotateY(180deg);
      background: $background-main;
      font-family: $secondary-font-family;
      color: $color-text;
      line-height: 1.5rem;
      font: {
         size: 15px;
      }
   }

   &__icon {
      margin: 0 10px;
      width: 100%;
      padding: 0 20px 20px;
      height: 124px;
      img {
         object-fit: contain;
         max-width: 100%;
         max-height: 100%;
      }
   }
}
