.section {
   img {
      width: 100%;
   }
   background-size: cover !important;
   &__wrapper {
      padding: 50px 0;
      &_we-produce {
         padding-top: 10px;
         padding-bottom: 20px;
      }
   }

   &__list {
   }

   &__icon {
      padding-right: 10px;
   }

   &__item {
      display: flex;
      align-items: center;
      padding: 10px 0;
   }

   &__header {
      font: {
         weight: 700;
         size: 27px;
         family: $primary-font-family;
      }

      @media screen and (min-width: 1023px) {
         font: {
            size: 56px;
         }
      }

      &.m-secondary {
      }
   }

   &__subheader {
      font: {
         weight: 300;
         family: $secondary-font-family;
      }
      letter-spacing: 5px;
      color: $background-dark-secondary;
   }

   &__text {
   }

   &__img {
      max-width: 800px;
      width: 100%;
      vertical-align: middle;
      margin: 0 auto 30px;
   }
}
