.form-section {
   position: relative;
   background: $background-dark-secondary;
   .form__wrapper {
      color: #686868 !important;
      background: $background-bright;
      border-radius: 10px;
      padding: 40px 50px 20px;
      label,
      label.wpforms-field-label,
      label.wpforms-field-label-inline {
         font-weight: 400 !important;
         margin-top: 5px !important;
      }
      label.wpforms-field-label-inline {
         font-family: georgia, serif;
      }
      .wpforms-field {
         padding: 5px 0 !important;
      }
      input,
      textarea {
         background-color: #f0f2f1 !important;
         border-radius: 0 !important;
         border: 1px solid transparent !important;
      }
   }
   .form__title {
      text-align: center;
      text-transform: uppercase;
      line-height: 1.4;
      font-weight: 400;
      margin-bottom: 10px;
   }
   .form__icon-wrap {
      display: flex;
      justify-content: center;
   }
   .form__icon {
      width: 29px;
      height: 29px;
   }
   .wpforms-submit {
      width: auto !important;
      color: #fff !important;

      display: block !important;
      margin: 0px 0px 0px 0px !important;
      padding: 3px 50px !important;
      font-family: "RobotoSlab", sans-serif !important;
      font-size: 24px !important;
      font-weight: 500 !important;
      border-radius: 3px 3px 3px 3px !important;
      background-color: #070707 !important;
      &:hover,
      &:focus {
         color: #050505 !important;
         background-color: #4dcd2d !important;
         outline: none !important;
      }
   }
}

.form__inner {
   max-width: 1110px;
   justify-content: center;
   align-items: center;
}

.footer__section_contacts-wrap {
   width: calc(53% - 20px);
   padding-left: 70px;
   @media screen and (max-width: 1000px) {
      padding-left: 20px;
   }
   @media screen and (max-width: 850px) {
      width: 100%;
      padding-left: 0;
      padding-bottom: 40px;
   }
}

.footer__section_form-wrap {
   width: calc(47% - 20px);
   padding-left: 70px;
   @media screen and (max-width: 1000px) {
      padding-left: 20px;
   }
   @media screen and (max-width: 850px) {
      width: 100%;
      padding-left: 0;
   }
}

.footer__section_contacts-wrap {
   display: flex;
   align-items: flex-start;
   .footer__section-title {
      text-align: center;
   }
}

@media screen and (max-width: 850px) {
   .footer__section_contacts {
      display: block;
      width: 100%;
      .nav {
         padding-left: 0;
         justify-content: center;
      }
   }
   .form-section .form__wrapper {
      padding-left: 25px;
      padding-right: 25px;
   }
}
@media screen and (max-width: 475px) {
   .form-section .footer__section-title,
   .form-section .footer__content {
      font-size: 20px;
   }
   .form-section .footer__content_tel {
      font-size: 16px;
   }

   .form-section .form__inner {
      padding-bottom: 50px;
   }
}
