.video {
   &__wrapper {
      padding-bottom: 30px;
   }

   &__list {
      margin-bottom: 30px;
      display: flex;
      align-items: center;
      margin: 0 -10px 0;
      flex-wrap: wrap;

      &.m-secondary {
         .video__item {
            width: calc(50% - 20px);

            @media screen and (min-width: $medium) {
               width: calc(33.33% - 20px);
            }
         }
      }
   }

   &__item {
      width: 100%;
      margin: 0 10px;
      position: relative;
      overflow: hidden;
      margin-bottom: 20px;

      &:before {
         content: "";
         padding-top: 56.1%;
         display: block;
         position: relative;
         z-index: -1;
      }

      &.m-large {
         width: 100%;
         max-width: 654px;
         margin: 0 auto;
      }

      @media screen and (min-width: $medium) {
         width: calc(33.33% - 20px);
      }
   }

   iframe,
   &__img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s;
      cursor: pointer;
   }

   &__img:hover {
      transform: scale(1.1);
   }
}

.fake-iframe {
   width: 100%;
   height: 100%;
   position: absolute;
   top: 0;
   bottom: 0;
   right: 0;
   left: 0;
   z-index: 1;
   background-position: center;
   background-size: cover;
   cursor: pointer;
   &__play {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 68px;
      height: 48px;
      svg {
         // background-color: #8e8e8e;
         // border: 1px solid #424242;
         // border-radius: 5px;
      }
   }
   .ytp-large-play-button-bg {
      -webkit-transition: fill 0.1s cubic-bezier(0.4, 0, 1, 1), fill-opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
      transition: fill 0.1s cubic-bezier(0.4, 0, 1, 1), fill-opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
      fill: #212121;
      fill-opacity: 0.8;
   }
   &:hover .ytp-large-play-button-bg {
      -webkit-transition: fill 0.1s cubic-bezier(0, 0, 0.2, 1), fill-opacity 0.1s cubic-bezier(0, 0, 0.2, 1);
      transition: fill 0.1s cubic-bezier(0, 0, 0.2, 1), fill-opacity 0.1s cubic-bezier(0, 0, 0.2, 1);
      fill: #f00;
      fill-opacity: 1;
   }
}
