.about {
   &__wrapper {
      padding: 50px 10px;
      max-width: 1150px;
      margin: 0 auto;
   }

   &__subheader {
      font: {
         family: $secondary-font-family;
         size: 13px;
         weight: 700;
      }

      line-height: 1rem;
      text-align: left;

      @media screen and (min-width: $medium) {
         font: {
            size: 23px;
         }
         line-height: 1.8rem;
      }
   }

   &__header {
      font: {
         weight: 700;
         size: 27px;
         family: $primary-font-family;
      }

      @media screen and (min-width: 1023px) {
         font: {
            size: 56px;
         }
      }

      &.m-secondary {
      }
   }

   &__list-title {
      font: {
         weight: 700;
         size: 18px;
         family: $secondary-font-family;
         style: italic;
      }
      text-transform: uppercase;

      @media screen and (min-width: 1023px) {
         font: {
            size: 18px;
         }
      }
      &.bMar-30 {
         margin-bottom: 30px;
      }
   }

   &__content {
      font: {
         weight: 400;
         size: 16px;
         family: $custom-font-family;
      }
      color: $secondary-text;
      text-transform: none;

      &.m-secondary {
         text-transform: uppercase;
         font: {
            family: $secondary-font-family;
            weight: 700;
            size: 15px;
         }
      }
   }

   &__signature {
      font: {
         weight: 700;
         size: 11px;
         family: $base-font-family;
      }
      color: $background-dark;
      text-transform: uppercase;
      margin-bottom: 30px;
   }
}
