.hero {
   &__wrapper {
      position: relative;
      margin-bottom: 50px;
      overflow: hidden;
      max-height: 680px;
      min-height: 420px;

      @media screen and (min-width: 1023px) {
         max-height: 680px;
      }

      &:before {
         content: "";
         padding-top: 100%;
         display: block;
      }
   }

   &__banner {
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(0, 0, 0, 0.5);
   }

   &__img {
      position: relative;
      width: 100%;
      max-width: 800px;
   }

   &__video {
      width: 100% !important;
      position: absolute;
      left: 0;
      top: 0;
      object-fit: cover;
   }
}
