.photo {
   &__list {
      display: flex;
      margin: 0 -10px 0;
      flex-wrap: wrap;

      &.m-secondary {
         .photo__item {
            &:first-child {
               display: none;
            }
         }
      }
   }

   &__item {
      width: 100%;
      margin: 0 5px;
      position: relative;
      overflow: hidden;
      margin-bottom: 10px;

      &:before {
         content: "";
         padding-top: 56.1%;
         display: block;
         position: relative;
      }

      &.m-large {
         width: 100%;
         max-width: 654px;
         margin: 0 auto;
      }
      @media screen and (min-width: 476) {
         width: calc(50% - 10px);
      }
      @media screen and (min-width: $medium) {
         width: calc(33.33% - 10px);
      }

      @media screen and (min-width: $large) {
         width: calc(25% - 10px);
      }
   }

   &__img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s;
      cursor: pointer;
   }

   &__img:hover {
      transform: scale(1.1);
   }
}
